
html {
  font-size: 16px;
}
body {
  margin: 0;
  overflow: hidden;
}
a {
  text-decoration: none;
}
a:hover, a:visited, a:link, a:active {
  text-decoration: none;
}
textarea {
  resize: none;
}
.Router {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transition: all .8s ease;
  transition: all .8s ease;
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-100%, 0);
  transform: translate(-100% 0);
}
#app {
  font-size: 100%;
  font-family: 'ArialMT', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  overflow: auto;
}
.tile {
  height: 19rem;
  width: 20rem;
  position: relative;
  margin-left: 1rem;
  border-style: solid;
}
.homePage {
  position: absolute;
  top: 10rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-top: 1rem;
}
.header {
  margin: 0 auto;
  width: 20rem;
}
.listItem {
  border-color: black;
  background-color: white;
  margin-top: 1px;
  width: 80%;
}
.homeHeader {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 60rem;
  overflow: hidden;
  -ms-touch-action: none;
      touch-action: none;
  content-zooming: none;
}
.homebody {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 10rem;
}
.homebodybox {
  height: 25rem;
  width: 25rem;
  margin: 6.25rem auto;
  position: relative;
  -webkit-transform: rotateX(0deg) rotateY(0deg);
          transform: rotateX(0deg) rotateY(0deg);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  cursor: move;
}
.homebodybox div {
  position: absolute;
  left: 0;
  right: 0;
  background-size: cover;
  background-position: center;
}
.homeManueText {
  font-size: 4rem;
  width: 22rem;
  position: relative;
  margin: auto;
  text-align: center;
  font-family: "Times New Roman", Times, serif;
  font-weight: 800;
  color: #fff;
  text-shadow: 1px 0px #009807, 1px 2px #006705, 3px 1px #009807, 2px 3px #006705, 4px 2px #009807, 4px 4px #006705, 5px 3px #009807, 5px 5px #006705, 7px 4px #009807, 6px 6px #006705, 8px 5px #009807, 7px 7px #006705, 9px 6px #009807, 9px 8px #006705, 11px 7px #009807, 10px 9px #006705, 12px 8px #009807, 11px 10px #006705, 13px 9px #009807, 12px 11px #006705, 15px 10px #009807, 13px 12px #006705, 16px 11px #009807, 15px 13px #006705, 17px 12px #009807;
}
.homeCard {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.cardstyleLeft {
  background-image: url(~static/img/1.png);
}
.cardstyleRight {
  background-image: url(~static/img/2.png);
}
.cardstyleTop {
  background-image: url(~static/img/3.png);
}
.cardstyleBottom {
  background-image: url(~static/img/4.png);
}
.cardstylePre {
  background-image: url(~static/img/5.png);
}
.cardstyleBack {
  background-image: url(~static/img/6.png);
}
.loading-container {
  height: 100%;
  opacity: 0.75;
  /* z-index: 999; */
  background-color: black;
}



